              p.fst-italic.mt-3 “Es  muy probable que la razón principal por la cual el derecho es un marco de referencia tan importante para las organizaciones LGBT sea la jurisprudencia. La Constitución de 1991, y en especial la interpretación que le dio la Corte Constitucional, han sido centrales para movilizar liderazgos, brindar un vocabulario y un escenario para hacer exigencias. Si bien la Constitución misma no menciona los derechos de los homosexuales, la Corte, en una serie de sentencias liberales, extendió los derechos a la igualdad y a la dignidad humana para incluir la protección contra la discriminación por orientación sexual. Con estas decisiones le dio un nuevo marco de significado a la orientación sexual de manera que ésta ha pasado de ser una cuestión de cultura y de “estilo de vida” a ser un problema de derechos.
                br
                br
